import React from 'react';
import { Link } from 'react-router-dom'

function Nav() {
    return (
        <nav>
            
            <h1>Hello </h1>
            <ul> <Link to = '/Details'>
                <li>Sign Up</li>
                </Link>
                <Link to =  '/SignIn'>
                <li>Sign In</li>
                </Link>
            </ul>
        </nav>
    )
}

export default Nav