import React from 'react';
import Counter from './Counter'
import Inputbar from './Inputbar'
import Label from './Label'
import { Typography, Avatar, Card, CardHeader, IconButton, CardMedia, CardContent, CardActions, Collapse} from '@material-ui/core/'
import { red } from '@material-ui/core/colors'
import {makeStyles} from '@material-ui/styles'
import MoreVertIcon from '@material-ui/icons/MoreVert';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import clsx from 'clsx';
import { BrowserRouter as Router, Link, Route } from 'react-router-dom';
import Profiles from './Perfiles/Profiles'
import Profiles2 from './Perfiles/Profiles2'




const useStyles = makeStyles((theme) => ({
    root: {
      maxWidth: 345,
    },
    media: {
      height: 200,
      paddingTop: '56.25%', // 16:9
    },  
   
      expandOpen: {
        transform: 'rotate(180deg)',
      },
   
    avatar: {
      backgroundColor: red[500],
    },
}));

function Post(i) {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);
  
    const handleExpandClick = () => {
      setExpanded(!expanded);
    };

    

    return (
        <div>
        

        {
            
                <div>
                  
                 <Card className={classes.root}>
                    
                 <CardHeader
        avatar={
          <Avatar aria-label="recipe" className={classes.avatar}>
            {i.avatar} 
          </Avatar>
        } 
        action={
            <IconButton aria-label="settings">
                <MoreVertIcon />
                </IconButton>
              }
          title={i.username}
          subheader="September 14, 2016"
        />
          <CardMedia
        className={classes.media}
        
        image = 'https://assets.tonica.la/__export/1602704308252/sites/debate/img/2020/10/14/eren-jaeger-portada.jpg_242310155.jpg'
        title= {i.username}
        
        
      />
                     
                     <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          
        {i.username}
        {i.caption}
        </Typography>
      </CardContent>
                    
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
          <FavoriteIcon />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
        
      </CardActions>
      
      <Label />
      </Card>

                    
                   
                    
                    
                    <Counter />
                    
                    
                     
                     

                    <h2> {i.link} </h2>
                    
                   
                    <Inputbar />

                    
                    </div>
                    
                
                
                
            
        }
        
        </div>
    )
}
export default Post;