import React from 'react';


function Profiles2 () {

  const profiles2 = [{
avatar : <img 
src = 'https://i.blogs.es/d13b4e/hollowknight/840_560.jpeg'
alt = ''
/>
  
,
username: "Manuel_14",
posts : <img className = 'size'
src = 'https://i.blogs.es/d13b4e/hollowknight/840_560.jpeg'
/>  }]
    

return (
        <div>
          {
            profiles2.map (i => (
              <div>
                <h2> {i.username} </h2>
                <h3>Posts of Manuel </h3>
                {i.posts}
              </div>
            )
         )
          
          }
          
          
        </div>
    )
}

export default Profiles2;