import React from 'react';

class Label extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            option: "reported" 
        };
        this.handleChange = this.handleChange.bind(this);
    } 
    handleChange = event => {
        this.setState({
            option: event.target.value
        })
    }

    handleSubmit = event => {
        alert (this.state.option)
        event.preventDefault()
    }

    render () {
return (
<form onSubmit = {this.handleSubmit}>
<div>
    
    <select className = 'Labels' 
    value = {this.state.option} onChange= {this.handleChange}>
<option value="reported">Report</option>
<option value= "You are not following this user anymore"> Stop Following</option>
<option value= "Go to post">Go to post</option>
</select>
<button Type= 'submit'> Submit</button>
</div>
</form>

)

    }
}

export default Label;     