import React from 'react';
import './App.css';
import Parent from './SignUpM/Parent'
import FormPersonalDetails from './CreateACount/FormPersonalDetails'
import Nav from './Nav'
import Profiles from './Perfiles/Profiles'
import Profiles2 from './Perfiles/Profiles2';
import { Route, BrowserRouter as Router, Link, Switch} from 'react-router-dom';




function App() {



  return (

<Router>
  <div>
    <Nav />
  <Switch>
  <Route path= '/Details' exact component= {FormPersonalDetails} />
  <Route path= '/SignIn' exact component= {Parent} />
  <Route path= '/Profiles' exact component= {Profiles} />
  <Route path= '/Profiles2' exact component= {Profiles2} />
  </Switch>
  
  </div>
  </Router>


  )

}




























    


export default App;
