import React from 'react';
import {Typography, Button, AppBar , Card, CardContent , CardMedia, CssBaseline , Grid, Toolbar, Container, TextField,} from '@material-ui/core';
import { PhotoCamera } from '@material-ui/icons'
import Parent from '../SignUpM/Parent'
import { Route, BrowserRouter as Router, Link} from 'react-router-dom';

class Onfirm extends React.Component {
    continue = event => {
        event.preventDefault();
        this.props.nextStep()
    }
    render() {
        const { values, handleChange} = this.props;
        return (
            
        <>

<CssBaseline />


    
    <Typography variant = "h6">
  Johan
    </Typography>


        
        
        <TextField id = 'Email'
        label = 'Enter Email'
        onChange = {handleChange('email')}
        />
        <br/>
        <TextField 
        id='Fullname'
        label=" Enter your Fullname"
        onChange = {handleChange('fullName')}
        />

        <TextField 
        id='Username'
        label="Create an username"
        onChange = {handleChange('userName')}
        />
        <TextField 
        id='Password'
        label="Create a password"
        onChange = {handleChange('passWord')}
        />



        <Button variant="contained" color="primary" onClick = {this.continue}> Continue </Button>

       

       

      

        </>
         


          
            
        )
    }
}


    


export default Onfirm;