import React from 'react';

class Counter extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            count: false
        };
        this.handleChange = this.handleChange.bind(this);
    } 
    handleChange(event) {
        event.preventDefault()
        this.setState((prevState) => ({
            count: !prevState.count 
        }))
    }
    
    render () {
        if (this.state.count) {
            return (
                <div> 
                    <form Name = "" Method = "" Action = "">
                        <input className = 'size2' 
                        Name= "IB1" type = "image" src= "https://cdn.vox-cdn.com/thumbor/Tu7MTsyg34xe7ihpFiNsxvmazvk=/0x0:640x426/1400x1400/filters:focal(269x162:371x264):format(jpeg)/cdn.vox-cdn.com/uploads/chorus_image/image/51868653/like.0.jpeg"  
                        alt = ""
                        onClick = {this.handleChange}/>
                    <h3 className = "Message"> You {this.state.count} liked this </h3>
                 
    
                    </form>
                
                </div>
        
       
    
    )
    }
 else {
    return (
        <div> 
            <form Name = "" Method = "" Action = "">
                <input className = 'size2' 
                Name= "IB1" type = "image" src= "https://cdn.vox-cdn.com/thumbor/Tu7MTsyg34xe7ihpFiNsxvmazvk=/0x0:640x426/1400x1400/filters:focal(269x162:371x264):format(jpeg)/cdn.vox-cdn.com/uploads/chorus_image/image/51868653/like.0.jpeg"  
                alt = ""
                onClick = {this.handleChange}/>
            <h3 className = "Message"> Nobody likes this yet {this.state.count}  </h3>
         

            </form>
        
        </div>
    )
 }
}
}

export default Counter; 