import React from 'react';


function Profiles () {

  const profiles = [{
avatar : <img 
src = 'https://i.blogs.es/d13b4e/hollowknight/840_560.jpeg'
alt = ''
/>
  
, 
username: "Johan_69",
posts : <img className = 'size'
src = 'https://lov.do/wp-content/uploads/2020/04/landscape-mountains-sky-4843193.jpg'
/>  }]
    

return (
        <div>
          {
            profiles.map (i => (
              <div>
                <h2> {i.username} </h2>
                <h3>Posts</h3>
                {i.posts}
              </div>
            )
         )
          
          }
          
          
        </div>
    )
}

export default Profiles