import React from 'react';
import {Typography, Button, AppBar , Card, CardContent , CardMedia, CssBaseline , Grid, Toolbar, Container, TextField, Link} from '@material-ui/core';
import Onfirm from '../CreateACount/Onfirm'
import { PhotoCamera } from '@material-ui/icons'

class FormUserDetails extends React.Component {
    continue = event => {
        event.preventDefault();
        this.props.nextStep()
    }
    render() {
        const { values, handleChange} = this.props;
        return (
            
        <>


        
        
        <TextField id = 'User Name'
        label = 'Enter Username'
        onChange = {handleChange('userName')}
        />
        <br/>
        <TextField 
        id='Password'
        label=" Enter password"
        onChange = {handleChange('passWord')}
        />
        <Button variant="contained" color="primary" onClick = {this.continue}> Sign in </Button>

        

      

        </>
         


          
            
        )
    }
}

export default FormUserDetails;